import ButtonGroup from "../../components/ButtonGroup/ButtonGroup";
import styled from 'styled-components'
import { isMobile } from "../../utils/utils";

const FilterWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-bottom: 70px;
  margin-right: 32px;
  .filter{
    justify-content: flex-end;
    .g-button {
      margin-left: 8px ;
    }
  }
`
const Filter = ({url,onChange}) => {
  const cycle = ['WEEKLY','MONTHLY']
  const filterButtonWidth = isMobile() ? 64 : 96;

  const cycleSelect = cycle => {
    const u = new URL(url);
    const params = u.searchParams
    if(params.has('cycle')){
      params.delete('cycle')
    }
    params.append('cycle',cycle)
    onChange(u.toString())
  }
  return (
    <FilterWrapper>
      <ButtonGroup groups={cycle} selectedBorderColor='rgba(205, 122, 55)' defaultValue='MONTHLY' 
                  defaultBorderColor='rgba(85, 119, 253, 0.1)'  bgColor='rgba(85, 119, 253, 0.1)' 
                  className='filter' width={filterButtonWidth} height='32' onChange={cycleSelect} outline outlineColor='rgba(205, 122, 55, 0.5)'/>
    </FilterWrapper>
  )
}
export default Filter;