import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import axios from 'axios'
import BarChart from '../../components/BarChart/BarChart'
import AreaChart from '../../components/AreaChart/AreaChart'
import './index.less'
import Table from '../../components/Table/Table'
import { formatAddress, isMobile } from '../../utils/utils'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import ButtonGroup from '../../components/ButtonGroup/ButtonGroup'
import styled from 'styled-components'
import Filter from './Filter';

const GET_POOL_URL=`${process.env.REACT_APP_INFO_HTTP_URL}/get_pools`
const GET_TRADE_HISTORY_URL= `${process.env.REACT_APP_INFO_HTTP_URL}/get_trade_history`
const WrapperClick = ({record,children,onClick}) => (<span style={{width : '100%',display : 'block',height: '44px',lineHeight: '44px'}} onClick={() => onClick(record)}>{children}</span>)

export default function Index(){
  const [allPoolData, setAllPoolData] = useState({})
  const [tradeHistoryUrl, setTradeHistoryUrl] = useState(GET_TRADE_HISTORY_URL)
  const history = useHistory()

  const columns = [
    {
      title: 'ZONE',
      dataIndex: 'catalog',
      key: 'catalog',
      width: '15%',
      render : (value,row) => <WrapperClick record={row} onClick={onRowClick} >{value && value.indexOf('-') > 0 ? value.substring(value.indexOf('-') + 1,value.length).toUpperCase() : ''}</WrapperClick>,
    },
    {
      title: 'POOL',
      dataIndex: 'catalog',
      key: 'pool',
      width: '15%',
      render : (value,row) => <WrapperClick record={row} onClick={onRowClick} >{value ? value.toUpperCase() : ''}</WrapperClick>,
    },
    {
      title: 'BASE TOKEN',
      dataIndex: 'bToken',
      key: 'bToken',
      width: '20%',
      render : (value,row) => <WrapperClick record={row} onClick={onRowClick} >{value ? value.split('|').join(' | ') : ''}</WrapperClick>,
    },
    {
      title: 'POOL ADDRESS',
      dataIndex: 'address',
      key: 'address',
      width: '15%',
      render : (value,row) => <WrapperClick record={row}  onClick={onRowClick} >{formatAddress(value)}</WrapperClick>,
    },
    {
      title: 'LIQUIDITY',
      dataIndex: 'liquidity',
      key: 'liquidity',
      width: '20%',
      render : (value,row) =><WrapperClick record={row}  onClick={onRowClick} > <DeriNumberFormat value={value} suffix={` ${row.bToken.split('|')[0]}`} decimalScale='2' /></WrapperClick>,
    },
    {
      title: 'TRADE VOLUME',
      dataIndex: 'notional',
      key: 'notional',
      width: '20%',
      render : (value,row) => <WrapperClick record={row}  onClick={onRowClick} ><DeriNumberFormat value={value} suffix={` ${row.bToken.split('|')[0]}`} decimalScale='2' /></WrapperClick>,
    }
  ];
  const loadAllPoolData = async () => {
    const res = await axios.get(GET_POOL_URL);
    if(res.status === 200 && res.data.data){
      setAllPoolData(res.data.data)
    }
  }

  const onRowClick = d => {
    history.push(`/info/${d.address}/${d.catalog}/${d.bToken}/${d.chain}`)
  }

  useEffect(() => {
    loadAllPoolData();
  }, [])
  return(
    <div className='info-main'>
      <div className='title'>Deri Overview</div>
      <div className='charts'>
        <div className='graph tvl'>
          <AreaChart title='TVL' url = {`${process.env.REACT_APP_INFO_HTTP_URL}/get_liquidity_history`}/>
        </div>
        <div className='graph volume24'>
          <Filter url={GET_TRADE_HISTORY_URL} onChange={url => setTradeHistoryUrl(url)}/>
          <BarChart title='Volume 24H' url = {tradeHistoryUrl} />  
        </div>  
      </div>
      <div className='list-by-chain'>
        {Object.keys(allPoolData).map((chain) => (
         <Table key={chain} columns={columns} title={chain} initialData={allPoolData[chain]}/>
        ))}
      </div>
    </div>
  )
}

