import React, { useState,useEffect } from 'react'
import axios from 'axios'
import {BarChart,XAxis,YAxis,Tooltip,Bar,CartesianGrid,ResponsiveContainer} from 'recharts'
import dateFormat  from 'dateformat';
import RechartTooltip from '../RechartTooltip/RechartTooltip';
import { dataFormater, isMobile } from '../../utils/utils';
import Button from '../Button/Button';
import ButtonGroup from '../ButtonGroup/ButtonGroup';

export default function RechartBar({url,title}){
  const [data, setData] = useState(null)
  const [lastItem, setLastItem] = useState({})
  const [parentRect, setParentRect] = useState({})
  const [barPosData, setBarPosData] = useState({})
  const loadData = async (url) => {
    const res = await axios.get(url)
    let data = []
    if(res.status === 200 && Array.isArray(res.data.data)){
      data = res.data.data.sort((item1,item2) => {
        if(item1.timestamp > item2.timestamp) {
          return 1
        } else if(item1.timestamp < item2.timestamp){
          return -1
        } else {
          return 0
        }
      })
      if(data.length > 0){
        const last24Volume = res.data.last_24h ? res.data.last_24h : data[data.length -1].value
        setLastItem({timestamp : data[data.length -1].timestamp , value : last24Volume})
      }
    }
    setData(data.map(d => ({time : d.timestamp,value : Number(d.value)})))
  }
  const tickFormatter = tick => {
    if(tick && tick !== 'auto') {
      return dateFormat(new Date(tick * 1000),'mm-dd') 
    } else {
      return ''
    }
  }

  useEffect(() => {
    const rect = document.querySelector('.graph').getBoundingClientRect()
    setParentRect(rect)
    loadData(url);
  }, [url])
  const interval = isMobile() ? 3 : 2
  return (
    data ? <ResponsiveContainer height={300}>
       <BarChart height={300} width={parentRect.width} data={data}>
        <defs>
          <linearGradient id="colorArea" x1="0" y1="0" x2="0" y2="1">
            <stop offset="5%" stopColor="#3756CD" stopOpacity={0.85}/>
            <stop offset="95%" stopColor="#3756CD" stopOpacity={0}/>
          </linearGradient>
        </defs>

      <CartesianGrid
        vertical={false}
        strokeDasharray="9"
        stroke="#203B60"
      />
      <XAxis dataKey="time"  axisLine={false} tickLine={false} tick={{fill: '#fff',fontSize : '14'}}  padding={{left : 12,right : 12}}  tickFormatter={tickFormatter} interval={interval}/>
      <YAxis dataKey='value'  axisLine={false} tickLine={false} tickFormatter={dataFormater} tick={{fill: '#fff',fontSize : '14'}}  padding={{top : 25}} />
      <Tooltip position={{x : 16,y : -106}}  
          cursor={{ y : barPosData.y - 10,height : barPosData.height + 10,stroke: '#fff', fill : '#3756CD', 
          strokeWidth: 0,fillOpacity : 0.5}} content={<RechartTooltip title={title} lastItem={lastItem}/>}/>
      <Bar type="monotone" dataKey="value" fill="#3756CD" radius={[10, 10, 0, 0]} fillOpacity={0.9} onMouseOver={(data) => {
          setBarPosData(data);
        }} />
    </BarChart>
  </ResponsiveContainer>
  : null
  )
}