import React, { useState,useEffect } from 'react'
import RCTable from 'rc-table';
import RCPagination from 'rc-pagination'
import 'rc-pagination/assets/index.less';
import localeInfo from 'rc-pagination/lib/locale/en_US';
import axios from 'axios'
import './table.less'
import Icon from '../Icon/Icon';
export default function Table({title,columns,initialData =[],url,pagination}){
  const [data, setData] = useState(initialData)
  const [page, setPage] = useState(1)
  const [count, setCount] = useState()
  const [pageSize, setPageSize] = useState(10)

  const loadData = async (url,page) => {
    let restUrl = url;
    if(pagination){
      restUrl = `${restUrl}&page=${page}&page_amount=${pageSize}`
    }
    const res = await axios.get(restUrl);
    if(res.status === 200 && Array.isArray(res.data.data)){
      setCount(res.data.count / pageSize)
      if(res.data.count % pageSize > 0 ){
        setCount(Math.floor(res.data.count / pageSize) +1)
      }
      setData(res.data.data)
      setPage(page)
    }
  }

  const onPage = (page) => {
    loadData(url,page)
  }

  useEffect(() => {
    if(url){
      loadData(url,1);
    } 
  }, [url])
  return(
    <div className='table'>
      <div className='t-title'>{title}</div>
      <RCTable data={data} columns={columns} footer={() => pagination && <Pagination page={page} count={count} onPage = {onPage}/> }/>
    </div>
  )
}

function Pagination({page,count,onPage}){
  return (
    <div className='pagination' >
      <RCPagination onChange={onPage}
        current={page}
        total={count}
        prevIcon={() => <Icon token='arrow-left'/>}
        nextIcon={() => <Icon token='arrow-right'/>}
        locale={localeInfo}
        />
    </div>
  )
}