import { convertToInternationalCurrencySystem } from "../../utils/utils";
import dateFormat  from 'dateformat';
import styled from "styled-components";

const Wrapper = styled.div`
&.custom-tooltip {
  padding: 0 0 0 10px;
  color: #E0ECFF;
  z-index: 2;
  .tip-title {
    font-size: 16px;
    color: #E0ECFF;
  }
  .value {
    font-size: 32px;
    font-weight : 700;
    font-family : Barlow;
  }
  .date {
    margin-top: 4px;
    margin-left: 4px;
    font-size: 14px;
    height: 14px;
  }
}
`

const RechartTooltip = ({title, active, payload, label,lastItem }) => {
  if (active && payload && payload.length) {
    return (
      <Wrapper className="custom-tooltip">
        <div className='tip-title'>{title}</div>
        <div className="value">{convertToInternationalCurrencySystem(payload[0].payload.value)}</div>
        <div className="date">{`${dateFormat(new Date(payload[0].payload.time * 1000),'yyyy-mm-dd')}(UTC)`}</div>
      </Wrapper>
    );
  } else if(title && lastItem.value){
    return (
      <Wrapper className="custom-tooltip">
        <div className='tip-title'>{title}</div>
        <div className="value">{convertToInternationalCurrencySystem(lastItem.value)}</div>
        <div className="date">{`${dateFormat(new Date(lastItem.timestamp * 1000),'yyyy-mm-dd')}(UTC)`}</div>
      </Wrapper>
    );
  }
  return null
};
export default RechartTooltip